<script setup>
import { ref, onMounted } from 'vue'

const drawer = ref(null)
const rail = ref(true)
const isAuthenticated = ref(false)
const items = [
        { title: 'Acceuil', icon: 'mdi-home-city', link: '/', hidden: false },
        { title: "Ajouter Tortue", icon: "mdi-plus-thick", link: "/add", hidden: true },
        { title: "À propos ?", icon: "mdi-information-outline", link: "/about", hidden: false },
        { title: "Connexion", icon: "mdi-login-variant", link: "/login", hidden: false },
        { title: "Logout", icon: "mdi-logout-variant", link: "/logout", hidden: true },
      ]
onMounted(() => {
  isAuthenticated.value = true; // Set the authentication status
  // for(item of items){item.hidden = !isAuthenticated.value};
  items[0].hidden = !isAuthenticated.value;
  items[1].hidden = !isAuthenticated.value;
  items[2].hidden = !isAuthenticated.value;
  items[3].hidden = isAuthenticated.value;
  items[4].hidden = !isAuthenticated.value;
})
</script>

<template>
  <v-card>
    <v-layout>
      <v-navigation-drawer v-model="drawer" :rail="rail" permanent @click="rail = false" theme="dark">
        <v-list-item prepend-avatar="/logo-dark.svg" title="TORTUE-213" nav>
          <template v-slot:append>
            <v-btn variant="text" icon="mdi-chevron-left" @click.stop="rail = !rail"></v-btn>
          </template>
        </v-list-item>

        <v-divider></v-divider>

        <v-list density="compact">
          <template v-for="(item, i) in items">
            <v-list-item :key="i" v-if="shouldDisplayItem(item)" :to="item.link" link :value="item" color="primary">
              <template v-slot:prepend>
                <v-icon :icon="item.icon"></v-icon>
              </template>
              <v-list-item-title v-text="item.title"></v-list-item-title>
            </v-list-item>
          </template>
        </v-list>
      </v-navigation-drawer>

      <v-app-bar>
        <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>
        <v-toolbar-title>Application</v-toolbar-title>
      </v-app-bar>

      <v-main>
        <router-view></router-view>
      </v-main>
    </v-layout>
  </v-card>
</template>
<script>
export default {
  setup() {
    return {
      isAuthenticated, // Set this to the authentication status
      drawer,
      rail,
      items,
    };
  },
  methods: {
    shouldDisplayItem(item) {
      // Add your condition here, e.g., based on authentication status
      if (!item.hidden) {
        return true;
      } else {
        return false;
      }
    }
  },
};
</script>